<template>
  <div class="pay">
    <mt-header title="结算" :style="{background: all.window.navigationBarBackgroundColor}">
      <router-link slot="left" to="">
        <mt-button icon="back" @click="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="goodInfo">
      <div class="left">
        <img :src="buyGoods.pic" alt="" style="width: 100px;max-height: 100%">
      </div>
      <div class="center">
          <p>{{buyGoods.name}}</p>
        <div class="right">
          <p>{{buyGoods.num}}件</p>
          <p>¥ {{buyGoods.price}}</p>
        </div>
      </div>
    </div>
    <p class="title">联系方式</p>
    <div>
      <mt-field label="姓名"   placeholder="请输入真实姓名"></mt-field>
      <mt-field label="电话"  type="number"  placeholder="请输入手机号码"></mt-field>
      <mt-field label="电子邮箱"  type="email" placeholder="用于接受订单状态"></mt-field>
    </div>
    <p class="title">收货地址</p>
    <div>
      <mt-field label="详细收货地址"  placeholder="例如街道、门牌"></mt-field>
      <mt-field label="邮编" placeholder="选填"></mt-field>
    </div>
    <p class="title">留言</p>
    <div>
      <mt-field label="留言"  placeholder="选填"></mt-field>
    </div>
    <footer class="btn">
      <mt-button  size="large" style="font-size: 15px;color: white;" :style="{background:all.window.navigationBarBackgroundColor}">提交订单</mt-button>
    </footer>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        color:'blue',
        sheetVisible:false,
      }
    },
    computed:{
      all(){
        return this.$store.getters.getAll;
      },
      buyGoods(){
        return this.$store.getters.getBuyGood;
      },
    },
    methods: {
      back(){
        this.$router.go(-1);
      },
      share(){
        this.sheetVisible = true;
      },
    }
  }
</script>
<style scoped>
  .goodInfo{
    height: 1.4rem;
    display: flex;
    align-items: center;
}
  .left{
    padding: 5px;
    border: 1px solid darkgrey;
    flex:1;
    margin: 0 0.2rem;
    text-align: center;
  }
  .center{
    flex: 4;
  }
  .right{
    display: flex;
    justify-content: space-between;
  }
  .right>p:nth-child(2){
    margin-right: 0.2rem;
  }
  .title{
    padding: 0.2rem;
    background: #f7f7f7;
  }
  .btn{
    width: 90vw;
    padding: 2px 0;
    position: fixed;
    bottom:0;
    color: white;
    margin:0 5vw;
  }
</style>



