<template>
  <div class="box">
    <div class="all" :style="{flexDirection:all}">
      <div @click="seeInfo(key)" class="info" v-for="(item,key) in goodsList">
        <img :src="item.pic" alt="" style="width: 42vw;max-height: 100%">
        <p style="margin-top: 0.25rem">{{item.name}}</p>
        <p>¥{{item.price}}</p>
      </div>
    </div>
  </div>
</template>
<script>
  export default{
    data(){
      return {
        layout:'row',
        lists:[
          {
            img:require('../../assets/01.png'),
            name:'棒球衣',
            price:"66.00"
          },
          {
            img:require('../../assets/02.png'),
            name:'潮流男士衬衣',
            price:"88.88"
          },
          {
            img:require('../../assets/03.png'),
            name:'潮流短靴',
            price:"111.11"
          },
          {
            img:require('../../assets/04.png'),
            name:'迷彩男裤',
            price:"86.86"
          }
        ]
      }
    },
    computed:{
      goodsList(){
        return this.$store.getters.getGoodsList;
      },
      all(){
          let data=this.$store.getters.getAll;
          if(data.ext.attr.layout==="0"){
//            方块
           this.layout="row"
          }else if(data.ext.attr.layout==="1"){
//            列表
            this.layout="column"
          }
        return this.layout;
      }
    },
    methods:{
      share(){
        this.sheetVisible=true;
      },
      seeInfo(key){
        let  url='/goodsInfo/'+key;
        this.$router.push(url);
      }
    }
  }
</script>
<style scoped>
  .all{
    width: 100vw;
    display: flex;
    /*flex-direction: column;*/
    flex-wrap: wrap;
  }
  .info{
    margin: 0.25rem;
    width: 42vw;
    padding: 0.2rem 0;
    text-align: center;
    background: white;
  }
  .info>p{
    text-align: left;
    margin-left: 0.35rem;
  }
</style>



