<template>
  <div class="googsInfo">
    <mt-header  :style="{background: all.window.navigationBarBackgroundColor}">
      <router-link to="/index" slot="left">
        <mt-button icon="back">返回</mt-button>
      </router-link>
      <mt-button icon="more" slot="right" @click="share"></mt-button>
    </mt-header>
    <mt-actionsheet
      cancelText="预览暂不开放分享功能"
      v-model="sheetVisible">
    </mt-actionsheet>
    <div class="info_content">
      <img :src="goodsList[key]['pic']" alt="" style="width:100%;max-height: 100%">
      <div class="info_info">
        <p>{{goodsList[key]["name"]}}</p>
        <p style="color:rgba(236,84,84,1);">¥{{goodsList[key]["price"]}}</p>
      </div>
      <div class="info_footer">
        <div @click="addShopCar">
          <img src="../assets/car.png" alt="">
        </div>
        <div @click="addCar" :style="{background:all.window.navigationBarBackgroundColor}">加入购物车</div>
        <div @click="buy" :style="{background:all.window.navigationBarBackgroundColor}">立即购买</div>
      </div>
    </div>
    <div class="num" v-if="shown" @click.self="close">
      <div class="add_num">
        <p>数量:</p>
        <p class="btn">
          <span class="cut dark" v-if="!show" >-</span>
          <span class="cut" @click="cut" v-if="show" >-</span>
          <span>{{num}}</span>
          <span class="add" @click="add">+</span>
        </p>
      </div>
      <div>
        <mt-button type="primary" size="large" style="font-size: 15px" @click="sure">确定</mt-button>
      </div>
    </div>
  </div>
</template>
<script>
export default{
    data(){
        return {
          color:'blue',
          sheetVisible:false,
          num:1,
          show:false,
          shown:false,
          flag:0,
          key:''
        }
    },
  computed:{
    goodsList(){
      return this.$store.getters.getGoodsList;
    },
    all(){
      return this.$store.getters.getAll;
    }
  },
  methods:{
    share(){
      this.sheetVisible=true;
    },
    cut(){
        this.num--;
    },
    add(){
      this.show=true;
      this.num++;
    },
    addCar(){
      this.flag=0;
      this.shown=true;
    },
    close(){
      this.shown=false;
    },
    buy(){
      this.shown=true;
      this.flag=1;
    },
    addShopCar(){
        let obj={};
        obj["name"]=this.goodsList[this.key].name;
        obj["pic"]=this.goodsList[this.key].pic;
        obj["price"]=this.goodsList[this.key].price;
        obj["num"]=1;
        this.$store.dispatch('goods',obj);
        this.$router.push('/shopCar')
    },
    sure(){
//        0加购物车 1 购买
        if(this.flag===1){
          let obj={};
          obj["name"]=this.goodsList[this.key].name;
          obj["pic"]=this.goodsList[this.key].pic;
          obj["price"]=this.goodsList[this.key].price;
          obj["num"]=1;
          this.$store.dispatch('buyGood',obj);
          let url ='/pay';
          this.$router.push(url)
        }else if(this.flag===0){
          let obj={};
          obj["name"]=this.goodsList[this.key].name;
          obj["pic"]=this.goodsList[this.key].pic;
          obj["price"]=this.goodsList[this.key].price;
          obj["num"]=this.num;
          this.$store.dispatch('goods',obj);
          this.$router.push('/shopCar')
        }
    }
  },
  watch:{
        num(val){
           val===1 ? this.show=false:this.show=true

        }
  },
  created(){
    this.key=this.$route.params.id-0;
  }
}
</script>
<style scoped>
  .info_info{
    padding: 0.2rem;
  }
  .info_info>p{
    font-size: 14px;
  }
  .info_info>p{
    margin: 0.1rem 0;
  }
  .info_footer{
    position: fixed;
    bottom: 0;
    width: 100vw;
    height:0.8rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .info_footer>div{
    flex: 1;
    font-size: 15px;
    color: white;
    text-align: center;
  }
  .info_footer>div:nth-child(1){
    flex:0 1 20%;
  }
  .info_footer>div:nth-child(2){
    height:0.8rem;
    line-height: 0.8rem;
    background-color: rgba(86,86,86,1);
  }
  .info_footer>div:nth-child(3){
    height:0.8rem;
    line-height: 0.8rem;
    background-color: rgba(29,29,29,1);
  }
  .num{
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background-color:rgba(29,29,29,0.8);
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
  }
  .add_num{
    height: 40px;
    background-color: white;
    /*line-height: 40px;*/
    display: flex;
    justify-content: space-between;
    align-items: center;
    animation: 2s first;
  }
  @keyframes first{
    0%{
      height: 0;
    }
    100%{
      height: 40px;
    }
  }
  .add_num>p{
    padding: 0 0.2rem;
  }
  .cut{
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid black;
    text-align: center;
    line-height: 18px;
    margin: 0 0.1rem;
  }
  .add{
    display: inline-block;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    border: 1px solid black;
    text-align: center;
    line-height: 18px;
    margin: 0 0.1rem;
  }
  .dark{
    border: 1px solid gainsboro;
  }
</style>



